"use client";
import React, { useState } from "react";

export default function AdminLogin({ onClose, onAdd }) {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(''); // 오류 메시지 상태


    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const res = await fetch("/api/login", {
                method: "POST",
                body: JSON.stringify({ email, password }),
                headers: { "Content-Type": "application/json" },
            });

            if (res.ok) {
                const data = await res.json();

                // ✅ 토큰 저장 (예: data.token 또는 data.accessToken 확인)
                localStorage.setItem("token", data.accessToken);  // 또는 data.accessToken

                onAdd(data); // 로그인 성공 시
            } else {
                setError("로그인 실패. 다시 시도하세요.");
            }
        } catch (err) {
            setError("서버 오류가 발생했습니다.");
        }
    };


    return (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-30">
            <div className="w-full max-w-sm p-6 bg-white rounded-lg shadow-md">
                <h2 className="mb-4 text-xl font-bold">관리자 로그인</h2>

                {/* ❗ 에러 메시지 출력 */}
                {error && (
                    <div className="mb-2 text-sm text-red-500">
                        {error}
                    </div>
                )}

                <form onSubmit={handleSubmit} className="space-y-4">
                    <input
                        type="email"
                        placeholder="이메일"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                        className="w-full p-2 border rounded"
                        required
                    />
                    <input
                        type="password"
                        placeholder="비밀번호"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        className="w-full p-2 border rounded"
                        required
                    />

                    <div className="flex justify-end space-x-2">
                        <button type="button" onClick={onClose} className="px-4 py-2 border rounded">
                            취소
                        </button>
                        <button type="submit" className="px-4 py-2 bg-[#9BCC47] text-white rounded">
                            로그인
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
}
